<template>
    <section class="page-container">
        <div class="page-content">
            <page-list-container />
        </div>
        <page-pagination />
    </section>
</template>

<script>
// модули
import store from '@/store'
import issues from '@/store/modules/issues'
import pagination from '@/store/modules/pagination'

// компоненты
import PageListContainer from './PageListContainer'
import PagePagination from './PagePagination'

// стили
import '@/assets/scss/components/PageContainer.scss'

store.registerModule('issues', { ...issues })
store.registerModule('pagination', { ...pagination })

export default {
    name: 'PageContent',
    components: { PagePagination, PageListContainer },
}
</script>
