<template>
    <header class="list-header space">
        <div class="list-count">{{ issues.itemsTotalCount }} Open</div>
        <div class="list-actions">
            <details class="list-menu">
                <summary>Sort</summary>
                <div class="list-menu-modal">
                    <div class="list-menu-header">
                        <span class="select-menu-title">Sort by</span>
                    </div>
                    <div class="list-menu-content">
                        <div
                            class="list-menu-item"
                            v-for="(item, index) in sortList"
                            @click="selectSort({ key: item.key })"
                            :key="index"
                            :class="{ active: item.key === issues.sort }"
                        >
                            {{ item.name }}
                        </div>
                    </div>
                </div>
            </details>
        </div>
    </header>
</template>

<script>
// стили
import '@/assets/scss/components/PageListContainer/PageListContainerHeader.scss'

// helpers
import { mapState, mapActions } from 'vuex'

export default {
    name: 'PageListContainerHeader',
    data() {
        return {
            sortList: [
                {
                    name: 'Newest',
                    key: 'created-desc',
                },
                {
                    name: 'Oldest',
                    key: 'created-asc',
                },
                {
                    name: 'Most commented',
                    key: 'comments-desc',
                },
                {
                    name: 'Least updated',
                    key: 'comments-asc',
                },
                {
                    name: 'Recently updated',
                    key: 'updated-desc',
                },
                {
                    name: 'Least recently updated',
                    key: 'updated-asc',
                },
                {
                    name: 'Most reactions',
                    key: 'reactions-smile-desc',
                },
            ],
        }
    },
    computed: {
        ...mapState('issues', ['issues']),
    },
    methods: {
        ...mapActions('issues', ['selectSort']),
        // закрытие выпадающиго списка
        closeDetails() {
            let details = document.querySelectorAll('details')
            details.forEach(detail => {
                detail.removeAttribute('open')
            })
        },
    },
    mounted() {
        window.addEventListener('click', this.closeDetails, false)
    },
    beforeDestroy() {
        window.removeEventListener('click', this.closeDetails, false)
    },
}
</script>
