const state = () => {
    return {
        page: 1,
        perPage: 30,
    }
}
const mutations = {
    currentPage: (state, { pageIndex }) => {
        state.page = pageIndex
    },
}
const actions = {
    goToPage: ({ commit, dispatch }, { pageIndex }) => {
        if (pageIndex !== false) {
            commit('currentPage', { pageIndex })
            return dispatch('issues/loadIssuesList', null, { root: true })
        }
    },
}

export default {
    namespaced: true,
    ignoreIfExists: true,
    state,
    mutations,
    actions,
}
