<template>
    <div class="page-pagination">
        <button
            class="page-pagination__btn prev desktop"
            @click="goPrev"
            :class="{ disabled: this.currentPage === 1 || this.pagesCount === 0 }"
        >
            <img src="@/assets/images/chevron-left-solid.svg" alt="" />
        </button>
        <ul class="page-pagination__list">
            <li
                v-for="(page, index) in pages"
                :class="page.class"
                :key="index"
                v-on:click.prevent="goPage({ pageIndex: page.index })"
            >
                {{ page.label }}
            </li>
        </ul>
        <button
            class="page-pagination__btn next desktop"
            @click="goNext"
            :class="{
                disabled: this.currentPage === this.pagesCount || this.pagesCount === 0,
            }"
        >
            <img src="@/assets/images/chevron-right-solid.svg" alt="" />
        </button>
        <div class="page-pagination__btns mobile">
            <button
                class="page-pagination__btn prev"
                @click="goPrev"
                :class="{ disabled: this.currentPage === 1 || this.pagesCount === 0 }"
            >
                <img src="@/assets/images/chevron-left-solid.svg" alt="" />
            </button>
            <button
                class="page-pagination__btn next"
                @click="goNext"
                :class="{
                    disabled: this.currentPage === this.pagesCount || this.pagesCount === 0,
                }"
            >
                <img src="@/assets/images/chevron-right-solid.svg" alt="" />
            </button>
        </div>
    </div>
</template>

<script>
// стили
import '@/assets/scss/components/PagePagination.scss'

// helpers
import { mapState, mapActions } from 'vuex'

export default {
    name: 'PagePagination',
    computed: {
        ...mapState('issues', ['issues']),
        ...mapState('pagination', ['page', 'perPage']),

        // текущая страница
        currentPage() {
            return this.page
        },

        // количество страниц
        pagesCount: function() {
            return Math.ceil(this.issues.itemsTotalCount / this.perPage)
        },

        pages() {
            const pages = []
            // отображать 2 страницы до и 2 страницы после текущей
            let firstIndex = Math.max(1, this.currentPage - 2)
            let lastIndex = Math.min(this.pagesCount, firstIndex + 4)
            for (let i = firstIndex; i <= lastIndex; i++) {
                pages.push({
                    index: i,
                    label: i,
                    class: i === this.currentPage ? 'active' : '',
                })
            }
            // троеточие после ссылки на 1 страницу
            if (firstIndex > 2) {
                pages.unshift({
                    index: false,
                    label: '...',
                    class: 'disabled',
                })
            }
            // ссылка на 1 страницу
            if (firstIndex > 1) {
                pages.unshift({
                    index: 1,
                    label: 1,
                    class: 'waves-effect',
                })
            }
            // троеточие перед ссылкой на последнюю страницу
            if (lastIndex < this.pagesCount - 1) {
                pages.push({
                    index: false,
                    label: '...',
                    class: 'disabled',
                })
            }
            // ссылка на последнюю страницу
            if (lastIndex < this.pagesCount) {
                pages.push({
                    index: this.pagesCount,
                    label: this.pagesCount,
                    class: 'waves-effect',
                })
            }
            return pages
        },
    },
    methods: {
        ...mapActions('pagination', ['goToPage']),

        // перейти на страницу
        goPage: function({ pageIndex }) {
            this.goToPage({ pageIndex })
        },

        // перейти на предыдущую страницу
        goPrev: function() {
            if (this.currentPage > 1) {
                this.goToPage({ pageIndex: this.currentPage - 1 })
            }
        },

        // перейти на следующую страницу
        goNext: function() {
            if (this.currentPage < this.pagesCount) {
                this.goToPage({ pageIndex: this.currentPage + 1 })
            }
        },
    },
}
</script>
