<template>
    <ul class="list-content space" :class="{ loader: issues.listLoading }">
        <li class="list-item" v-for="(item, key) in issuesList" :key="key">
            <div class="list-item__content">
                <div class="list-item__link">
                    <a href="#" class="list-item__link-title">
                        {{ item.title }}
                    </a>
                    <a
                        href="#"
                        v-for="(label, key) in item.labels"
                        :key="key"
                        class="tag"
                        :style="{
                            'background-color': '#' + label.color,
                            color: '#' + getContrastYIQ(label.color),
                        }"
                    >
                        {{ label.name }}
                    </a>
                </div>
                <div class="list-item__small-text">
                    #{{ item.number }} opened {{ date(item.created_at) }} by
                    {{ authorName(item.user) }}
                </div>
            </div>
            <div class="list-item__comments" v-if="item.comments > 0">
                <div class="list-item__comments-icon"></div>
                <div class="list-item__comments-count">
                    {{ item.comments }}
                </div>
            </div>
        </li>
    </ul>
</template>

<script>
// стили
import '@/assets/scss/components/PageListContainer/PageListContainerContent.scss'

// helpers
import { mapState, mapActions } from 'vuex'

export default {
    name: 'PageListContainerContent',
    computed: {
        ...mapState('issues', ['issues']),

        // Список вопросов
        issuesList() {
            return this.issues.list
        },
    },
    methods: {
        ...mapActions('issues', ['loadIssuesList']),

        // Вывод имени автора вопроса
        authorName(item) {
            return item.login
        },

        // Обработка даты создания вопроса
        date(date) {
            let currentDate = new Date()
            let pastDate = new Date(date)
            let currentYear = currentDate.getFullYear()
            let yearCreatedIssue = pastDate.getFullYear()
            let timeDiff = Math.abs(currentDate.getTime() - pastDate.getTime())
            let daysPassed = Math.floor(timeDiff / (1000 * 3600 * 24))
            let day = pastDate.toLocaleString('en-US', { day: 'numeric' })
            let month = pastDate.toLocaleString('en-US', { month: 'short' })
            let year = pastDate.toLocaleString('en-US', { year: 'numeric' })

            if (daysPassed === 0) {
                return ' today'
            }
            if (daysPassed === 1) {
                return ' yesterday'
            }
            if (daysPassed > 1 && daysPassed <= 31) {
                return daysPassed + ' days ago'
            }
            if (daysPassed > 31 && yearCreatedIssue === currentYear) {
                return 'on ' + day + ' ' + month
            }
            return 'on ' + day + ' ' + month + ' ' + year
        },

        // Контраст текста в тегах
        getContrastYIQ(hexcolor) {
            let r = parseInt(hexcolor.substr(0, 2), 16)
            let g = parseInt(hexcolor.substr(2, 2), 16)
            let b = parseInt(hexcolor.substr(4, 2), 16)
            let yiq = (r * 299 + g * 587 + b * 114) / 1000
            return yiq >= 128 ? '000' : 'fff'
        },
    },
    beforeMount() {
        // Загрузка списка вопросов
        this.loadIssuesList()
    },
}
</script>
